/**
 * Base
 */
html,
body
{
    width: 100%;
    height: 100%;
}

html
{
    font-size: 16px;

    -ms-touch-action: manipulation;
        touch-action: manipulation;
}

body
{
    font-family: 'Open Sans', 'Trebuchet MS', sans-serif;
    font-size: 100%;
    font-weight: 400;
    line-height: 1.45;

    color: #212121;
}

/* Avoid empty images to appear as broken */
img:not([src]):not([srcset])
{
    visibility: hidden;
}

/**
 * Typography
 */
a
{
    transition: all .25s cubic-bezier(.4, 0, 1, 1);
    text-decoration: none;
}

a:focus,
a:hover
{
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6
{
    font-family: 'Raleway', 'Open Sans', 'Trebuchet MS', sans-serif;
}

ul
{
    padding: 0;

    list-style-type: none;
}

/**
 * Animations atom
 */
@keyframes gradient-animation
{
    0%
    {
        background-position: 50% 0;
    }
    50%
    {
        background-position: 51% 100%;
    }
    100%
    {
        background-position: 50% 0;
    }
}

/**
 * Buttons atom
 */
.btn__container
{
    text-align: center;
}

.btn
{
    font-size: .875rem;

    cursor: pointer;
    transition: all .25s cubic-bezier(.4, 0, 1, 1);
    text-transform: uppercase;

    border-radius: 4px;
    box-shadow: 0 15px 35px rgba(50, 50, 93, .1), 0 5px 15px rgba(0, 0, 0, .07);
}

.btn:active,
.btn:focus,
.btn:hover
{
    color: #fff;
    box-shadow: 0 26px 35px rgba(50, 50, 93, .1), 0 14px 15px rgba(0, 0, 0, .07);
}

.btn--fab
{
    font-size: 1.5rem;

    display: block;

    text-align: center;

    color: #fff;
    border-radius: 50%;
}

.btn--fab--primary
{
    background-color: #2196f3;
}

.btn--fab--primary:focus,
.btn--fab--primary:hover
{
    background-color: #43a6f5;
}

.btn--fab--primary:active
{
    background-color: #0c84e4;
}

.btn--fab--big
{
    width: 3.5rem;
    height: 3.5rem;
    padding: 1rem;
}

.btn--fab--medium
{
    font-size: 1.5rem;

    width: 3rem;
    height: 3rem;
    padding: .35rem;
}

.btn--fab--small
{
    width: 2.5rem;
    height: 2.5rem;
    padding: .35rem;
}

.btn--full,
.btn--ghost
{
    font-weight: 700;

    padding: .5625rem 2.625rem;

    border: 2px solid;
}

.btn--full--black
{
    color: #fff;
    border-color: #181818;
    background-color: #181818;
}

.btn--full--black:focus,
.btn--full--black:hover
{
    border-color: #3e3e3e;
    background-color: #3e3e3e;
}

.btn--full--primary
{
    color: #fff;
    border-color: #2196f3;
    background-color: #2196f3;
}

.btn--full--primary:focus,
.btn--full--primary:hover
{
    border-color: #6ab8f7;
    background-color: #6ab8f7;
}

.btn--full--white
{
    color: #212121;
    border-color: #fff;
    background-color: #fff;
}

.btn--full--white:focus,
.btn--full--white:hover
{
    color: #474747;
}

.btn--ghost
{
    background: transparent;
}

.btn--ghost--black
{
    color: #181818;
    border-color: #181818;
}

.btn--ghost--black:focus,
.btn--ghost--black:hover
{
    color: #3e3e3e;
    border-color: #3e3e3e;
}

.btn--ghost--primary
{
    color: #2196f3;
    border-color: #2196f3;
}

.btn--ghost--primary:focus,
.btn--ghost--primary:hover
{
    color: #6ab8f7;
    border-color: #6ab8f7;
}

.btn--ghost--white
{
    color: #fff;
}

.btn--ghost--white:focus,
.btn--ghost--white:hover
{
    color: #f5f5f5;
}

.btn--round,
.el--round
{
    border-radius: 50px;
}

/**
 * Dividers atom
 */
.divider
{
    max-width: 4rem;
    height: 2px;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;

    border: 0;
    background-color: #2196f3;
}

.divider--left
{
    margin-left: 0;
}

/**
 * Icons atom
 */
.icon
{
    cursor: pointer;
    transition: color .25s cubic-bezier(.4, 0, 1, 1);

    color: #a7cef7;
}

.icon:focus,
.icon:hover
{
    color: #fff;
}

.icon--menu
{
    float: right;
}

.process__icon
{
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.process__icon img
{
    max-width: 5.625rem;
    max-height: 5.625rem;
}

@media screen and (max-width: 767px)
{
    .process__info
    {
        margin-right: auto;
        margin-left: auto;
    }
}

/**
 * Links atom
 */
.link--default
{
    color: #86878b;
}

.link--default:focus,
.link--default:hover
{
    color: #212121;
}

.link--black
{
    color: #212121;
}

.link--black:focus,
.link--black:hover
{
    color: #f5f5f5;
}

.link--gold
{
    color: #f5f5f5;
}

.link--gold:focus,
.link--gold:hover
{
    color: #212121;
}

.link--white
{
    color: #fff;
}

.link--white:focus,
.link--white:hover
{
    color: #757575;
}

/**
 * Typed
 */
.no-js .js-typed
{
    display: none;
}

.no-js .js-typed-fallback
{
    display: block;
}

.js .js-typed-fallback
{
    display: none;
}

/**
 * Cards molecule
 */
.card
{
    display: flex;

    padding: 1rem 2rem;

    border-radius: 4px;
    background-color: #fff;
    box-shadow: 0 15px 35px rgba(50, 50, 93, .1), 0 5px 15px rgba(0, 0, 0, .07);

    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

@media screen and (max-width: 702px)
{
    .card:nth-of-type(2)
    {
        margin-top: 2.5rem;
    }
}

@media screen and (max-width: 1070px)
{
    .card:last-of-type
    {
        margin-top: 2.5rem;
    }
}

.card--hero
{
    margin-right: auto;
    margin-left: auto;
}

@media screen and (min-width: 769px)
{
    .card--hero
    {
        float: right;
    }
}

.card--long
{
    width: 16.5rem;
    min-height: 24rem;
    padding: 2rem 2rem 3rem;
}

.card--small
{
    max-width: 22.5rem;
}

.card--square
{
    width: 21.5rem;
    min-height: 16.5rem;
}

/**
 * Countdown molecule
 */
.countdown
{
    font-size: 1.75rem;
    font-weight: 500;

    display: table;

    margin: 0 auto 1.5rem;
}

.countdown > div
{
    float: left;

    min-width: 5rem;

    color: #fff;
}

@media screen and (max-width: 369px)
{
    .countdown > div:nth-of-type(3)
    {
        clear: left;
    }
}

.countdown span
{
    font-size: 1rem;

    position: relative;

    display: block;

    margin-top: .375rem;
    padding-top: .125rem;

    text-align: center;

    color: #fff;
}

.countdown span:before
{
    position: absolute;
    top: -.25rem;
    right: 0;
    left: 0;

    width: 1.25rem;
    height: .0625rem;
    margin-right: auto;
    margin-left: auto;

    content: '';

    background: #fff;
}

/**
 * Features molecule
 */
.features__list
{
    display: flex;

    align-items: center;
    flex-wrap: wrap;
}

@media screen and (max-width: 767px)
{
    .features__list > div
    {
        margin-bottom: 1rem;
    }
}

@media screen and (max-width: 767px)
{
    .features__list p
    {
        margin-right: auto;
        margin-left: auto;
    }
}

.features__mockup
{
    display: flex;

    justify-content: space-around;
    flex-wrap: wrap;
}

.features__mockup img
{
    max-width: 100%;
}

.features__icon
{
    padding: .75rem .8125rem;

    color: #fff;
    border-radius: 50%;
    background-color: #2196f3;
}

/**
 * Forms molecule
 */
.form-group
{
    position: relative;

    margin-bottom: 2rem;
    padding-left: 0;
}

@media screen and (max-width: 550px)
{
    .form-group:first-of-type
    {
        margin-bottom: 2rem;
    }
}

label,
.form-label
{
    font-size: .875rem;
    font-weight: 400;

    position: absolute;
    z-index: 1;
    top: .5rem;
    left: 0;

    display: block;

    margin-bottom: 0;

    transition: all .25s cubic-bezier(.4, 0, 1, 1);

    color: #757575;
}

input,
select,
textarea,
.form-element
{
    font-family: 'Open Sans', 'Trebuchet MS', sans-serif;

    position: relative;
    z-index: 3;

    display: block;

    width: 100%;
    margin-bottom: .5rem;
    padding-bottom: .5rem;

    border-top: 0;
    border-right: 0;
    border-bottom: 1px solid #e0e0e0;
    border-left: 0;
    background-color: transparent;
}

input:focus,
select:focus,
textarea:focus,
.form-element:focus
{
    border-bottom-color: #2196f3;
    outline: 0;
}

.form-element:focus + label,
.form-element:focus + .form-label
{
    font-size: .75rem;
    font-weight: 700;

    transform: translateY(-26px);

    color: #2196f3;
}

.form textarea
{
    resize: vertical;
}

.form--contact textarea
{
    min-height: 6.25rem;
}

@media screen and (min-width: 768px)
{
    .form--contact textarea
    {
        min-height: 12.1875rem;
    }
}

.form--hero
{
    width: 100%;
    max-width: 15.9375rem;
    margin-top: 16px;
    margin-right: auto;
    margin-left: auto;
}

.form--optin
{
    max-width: 30rem;
    margin-right: auto;
    margin-left: auto;
}

.form--optin .form-group
{
    position: relative;
}

.form--optin input
{
    padding-top: .9375rem;
    padding-right: 1.75rem;
    padding-bottom: .9375rem;
    padding-left: 1.75rem;

    border: 1px solid #fff;
    background-color: #fff;
    box-shadow: 0 15px 35px rgba(50, 50, 93, .1), 0 5px 15px rgba(0, 0, 0, .07);
}

.form--optin input:focus
{
    border: 1px solid #2196f3;
}

@media screen and (min-width: 440px)
{
    .form--optin input
    {
        padding-right: 11.75rem;
    }
}

@media screen and (max-width: 439px)
{
    .form--optin .btn
    {
        width: 100%;

        box-shadow: 0 15px 35px rgba(50, 50, 93, .1), 0 5px 15px rgba(0, 0, 0, .07);
    }
}

@media screen and (min-width: 440px)
{
    .form--optin .btn
    {
        position: absolute;
        z-index: 3;
        top: .1875rem;
        right: .25rem;

        box-shadow: none;
    }
}

/**
 * Heroes molecule
 */
.hero
{
    position: relative;

    background-position: center center;
    background-size: cover;
}

@media screen and (min-width: 768px)
{
    .hero
    {
        height: 100vh;
        min-height: 40rem;
    }
}

.hero--gradient
{
    background: #56ccf2;
    /* fallback for old browsers */
    background: linear-gradient(to top, #56ccf2, #2f80ed);
    /* uncomment the lines below to turn on animated gradient */
    /* background-size: 400% 400%;
  animation: gradient-animation 15s ease infinite;*/
}

.hero--gradient .hero__overlay
{
    background-color: rgba(0, 0, 0, .01);
}

.hero--image
{
    background-image: url(../images/bcgs/hero.jpg);
}

@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min--moz-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx)
{
    .hero--image
    {
        background-image: url(../images/bcgs/hero--@2.jpg);
    }
}

.hero--optin
{
    height: 100vh;
    min-height: 50rem;
}

@media screen and (min-width: 540px)
{
    .hero--optin
    {
        min-height: 33.75rem;
    }
}

.hero--home
{
    min-height: 71rem;
}

@media screen and (min-width: 555px)
{
    .hero--home
    {
        min-height: 60rem;
    }
}

.hero__overlay
{
    position: absolute;
    z-index: 1;

    width: 100%;
    height: 100%;

    background-color: rgba(0, 0, 0, .45);
}

.hero__content
{
    position: relative;
    top: 50%;

    width: 100%;

    transform: translateY(-50%);
}

.hero__content .list
{
    align-items: center;
}

.hero__content h1
{
    font-size: 3rem;
    font-weight: 300;

    text-transform: uppercase;

    color: rgba(255, 255, 255, .95);
}

@media screen and (max-width: 767px)
{
    .hero__content h1
    {
        text-align: center;
    }
}

.hero__content p
{
    margin-bottom: 28px;

    color: rgba(255, 255, 255, .75);
}

@media screen and (max-width: 767px)
{
    .hero__content p
    {
        margin-right: auto;
        margin-left: auto;

        text-align: center;
    }
}

@media screen and (min-width: 768px)
{
    .hero__content .card
    {
        margin-top: 0;
    }
}

/* Hero - Carousel */
.hero--carousel
{
    overflow: hidden;
}

@keyframes fade-in
{
    0%
    {
        opacity: 0;
    }
    100%
    {
        opacity: 1;
    }
}

.hero--carousel .carousel__wrapper,
.hero--carousel .carousel-inner,
.hero--carousel .item,
.hero--carousel .carousel__item
{
    height: 100%;
    min-height: 71rem;
}

@media screen and (min-width: 555px)
{
    .hero--carousel .carousel__wrapper,
    .hero--carousel .carousel-inner,
    .hero--carousel .item,
    .hero--carousel .carousel__item
    {
        min-height: 60rem;
    }
}

.hero--carousel .item
{
    transition: opacity .95s;
}

.hero--carousel .item.active
{
    animation: fade-in .95s;
}

.hero--carousel .carousel__item
{
    width: 100%;
    height: 100%;

    background-size: cover;
}

.hero--carousel .carousel__item-one
{
    background: url(../images/carousel/carousel-slide-one.jpg) no-repeat center center;
}

.hero--carousel .carousel__item-two
{
    background: url(../images/carousel/carousel-slide-two.jpg) no-repeat center center;
}

.hero--carousel .carousel__item-three
{
    background: url(../images/carousel/carousel-slide-three.jpg) no-repeat center center;
}

.hero--carousel .carousel__item-four
{
    background: url(../images/carousel/carousel-slide-four.jpg) no-repeat center center;
}

/* Hero -  ParticlesJS */
.hero--particlesjs
{
    background-color: #fff;
    background-image: none;
}

.hero--particlesjs .hero__overlay
{
    background-color: rgba(255, 255, 255, .25);
}

.hero--particlesjs .hero__content h1,
.hero--particlesjs .hero__content p
{
    color: #212121;
}

.particles
{
    position: absolute;
    top: 0;
    left: 0;

    display: block;

